"use client"

import { useState, useEffect, use } from "react"

export type CartItem = {
    id: string | number;
    name: string;
    quantity: number;
    image? : string | null;
}

export function useCart() {
    const [cart, setCart] = useState<CartItem[]>([]);
    const [isLoaded, setIsLoaded] = useState(false);

    // –ó–∞–≥—Ä—É–∑–∫–∞ –∫–æ—Ä–∑–∏–Ω—ã –∏–∑ localStorage –ø—Ä–∏ –º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏
    useEffect(() => {
        const savedCart = localStorage.getItem("cart");
        if (savedCart) {
            try{
                setCart(JSON.parse(savedCart));
            } catch (e) {
                console.error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–∞—Ä—Å–∏–Ω–≥–µ —Å–æ—Ö—Ä–∞–Ω—ë–Ω–Ω–æ–π –∫–æ—Ä–∑–∏–Ω—ã:", e);
            }
        }
        setIsLoaded(true);
    }, []);

    // –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –≤ localStorage –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏
    useEffect(() => {
        if (isLoaded){
            localStorage.setItem("cart", JSON.stringify(cart));
        }
    },[cart,isLoaded]);

    const addToCart = (product: Omit<CartItem, 'quantity'>) => {
        setCart(prevCart => {
            const existingItem = prevCart.find(item => item.id === product.id);

            if (existingItem) {
                return prevCart.map(item => 
                    item.id === product.id
                    ? { ...item, quantity: item.quantity + 1 }
                    : item
                )
            } else{
                return [...prevCart, {...product, quantity: 1}];
            }
        });
    };

    const updateQuantity = (productId: string | number, quantity: number) => {
        setCart(prevCart => {
            if(quantity <= 0){
                removeFromCart(productId);
                return prevCart;
            }
            return prevCart.map(item => item.id === productId ? { ...item, quantity } : item);
        })
    }

    const clearCart = () => {
        setCart([]);
    }

    const removeFromCart = (productId: string | number) => {
        setCart(prevCart => prevCart.filter(item => item.id !== productId));
    }

    const getItemCount = () => {
        return cart.reduce((total, item) => total + item.quantity, 0);
    }


    return {
        cart,
        addToCart,
        updateQuantity,
        clearCart,
        removeFromCart,
        getItemCount

    }
}