"use client"

import React, { useEffect, useRef, useState } from "react";
import Link from "next/link";
import { signIn, signOut, useSession } from "next-auth/react";
import { useCartContext } from "../cart/CartContext";

export default function Header(){
    const [open, setOpen] = useState(false);
    const { data: session } = useSession();
    const menuRef = useRef<HTMLDivElement | null>(null);

    const [openSearch, setOpenSearch] = useState(false);
    const [query, setQuery] = useState("");
    const [results, setResults] = useState<Array<{id: string | number; name: string}>>([]); // –†–µ–∑—É–ª—å—Ç–∞—Ç—ã –ø–æ–∏—Å–∫–∞
    const [loading, setLoading] = useState(false);
    const cacheRef = useRef<Record<string, Array<{id: string | number; name: string}>>>({}); // –ö—ç—à –¥–ª—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤ –ø–æ–∏—Å–∫–∞

    const { getItemCount } = useCartContext();

    useEffect(() => {
        function handleOutside(e: MouseEvent) {
            if (!menuRef.current) return;
            if (e.target instanceof Node && !menuRef.current.contains(e.target)) {
                setOpen(false);
            }
        }

        function handleKey(e: KeyboardEvent) {
            if (e.key === "Escape") setOpen(false);
        }

        document.addEventListener("mousedown", handleOutside);
        document.addEventListener("keydown", handleKey);
        return () => {
            document.removeEventListener("mousedown", handleOutside);
            document.removeEventListener("keydown", handleKey);
        };
    }, []);

    useEffect(() => {
        if (query.length < 2){
            setResults([]);
            return;
        }
        if (cacheRef.current[query]){
            setResults(cacheRef.current[query]);
            return;
        }

        const timer = setTimeout(async () => {
            setLoading(true);
            try{
                const res = await fetch(`/api/searchRoute?q=${query}`);
                const data = await res.json();
                cacheRef.current[query] = data;
                setResults(data);
            } finally{
                setLoading(false);
            }
        }, 300);
        return () => clearTimeout(timer);
    }, [query]);

    return (
        <header className="bg-gray-800 text-white">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                <div className="flex items-center justify-between h-16">
                    <Link href="/" className="flex items-center gap-3">
                        <span className="sr-only">–ì–ª–∞–≤–Ω–∞—è</span>
                        <svg className="w-8 h-8 text-indigo-400" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" aria-hidden>
                            <rect x="3" y="3" width="18" height="18" rx="4" fill="currentColor" />
                        </svg>
                        <strong className="text-white">–õ–æ–≥–æ—Ç–∏–ø</strong>
                    </Link>

                    <nav className="hidden md:flex gap-6 text-sm justify-center items-center">
                        <Link href="/" className="text-gray-200 hover:underline">–ì–ª–∞–≤–Ω–∞—è</Link>
                        <Link href="/about" className="text-gray-200 hover:underline">–û –Ω–∞—Å</Link>
                        <Link href="/catalog" className="text-gray-200 hover:underline">–£—Å–ª—É–≥–∏</Link>
                        <Link href="/admin" className="text-gray-200 hover:underline">Admin</Link>

                    {/* –ü–æ–∏—Å–∫ */}
                        <div className="relative">
                            <button className="flex items-center text-gray-200 "
                                onClick={() => setOpenSearch((v) => !v)}
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="size-4">
                                    <path strokeLinecap="round" strokeLinejoin="round" d="m21 21-5.197-5.197m0 0A7.5 7.5 0 1 0 5.196 5.196a7.5 7.5 0 0 0 10.607 10.607Z" />
                                </svg>
                            </button>

                            <div className={`absolute top-1/2 -translate-y-1/2 p-2 left-full w-64 rounded shadow-lg transition-all duration-100 ${
                                openSearch ? "opacity-100 translate-x-0" : "opacity-0 -translate-x-4 pointer-events-none"}
                            }`}>
                                <input
                                    value={query}
                                    onChange={(e) => setQuery(e.target.value)}
                                    type="text"
                                    placeholder="–ü–æ–∏—Å–∫..."
                                    className="w-full border p-2 rounded "
                                    autoFocus
                                />
                            </div>

                            {results.length > 0 && (
                            <ul className="absolute left-full w-64 mt-2 bg-white text-black rounded shadow divide-y">
                                {results.map((p) => (
                                <li key={p.id}>
                                    <Link
                                    href={`/product/${p.id}`}
                                    className="flex items-center gap-3 p-2 hover:bg-gray-100"
                                    >
                                    <div>
                                        <div className="text-sm font-medium">{p.name}</div>
                                    </div>
                                    </Link>
                                </li>
                                ))}
                            </ul>
                            )}

                        </div>
                    </nav>


                    <div className="relative" ref={menuRef}>
                        {!session ? (
                            <>
                                <button
                                    aria-expanded={open}
                                    aria-haspopup="menu"
                                    onClick={() => setOpen((v) => !v)}
                                    className="px-3 py-1 rounded bg-indigo-600 hover:bg-indigo-500 text-sm"
                                >
                                    –í—Ö–æ–¥
                                </button>

                                {open && (
                                    <div className="absolute right-0 mt-2 z-50 bg-white text-black dark:bg-gray-900 dark:text-white p-4 rounded shadow-lg w-44">
                                        <div className="text-sm mb-2">–í–æ–π—Ç–∏ —Å –ø–æ–º–æ—â—å—é</div>
                                        <button className="w-full text-left py-1 hover:bg-gray-100 rounded" onClick={() => signIn("github")}>
                                            GitHub
                                        </button>
                                        <button className="w-full text-left py-1 hover:bg-gray-100 rounded" onClick={() => signIn("google")}>
                                            Google
                                        </button>
                                    </div>
                                )}
                            </>
                        ) : (
                            <div className="flex items-center gap-3">
                                <span className="text-sm">{session.user?.name ?? session.user?.email}</span>
                                <button className="px-3 py-1 rounded border text-sm" onClick={() => signOut()}>
                                    –í—ã—Ö–æ–¥
                                </button>
                            </div>
                        )}
                        
                        {/* –ö–æ—Ä–∑–∏–Ω–∞ */}
                        <div>
                            <Link href="/cart" className="flex items-center text-gray-200 hover:underline text-[12px] ">
                                <div className="mx-auto">–ö–æ—Ä–∑–∏–Ω–∞ {getItemCount()}</div>
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </header>
    );
}